@import url(https://fonts.googleapis.com/css2?family=Bungee&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Anton&family=Fredoka+One&family=Righteous&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  min-height: 650px;
}

/* SideBar.js */
.sidebar {
    text-align: center;
    display: inline-block;
    position: relative;
    /* max-width: %; */
    /* background-color: #e2d6ff; */
    background-color: #2e1d1d;
    color: #FFFFFF;
    /* border: 1px solid #F1F4FFFF; */
    width: 250px;
    /* max-width: 60%; */
    padding: 50px;
    padding-left: 0;

}

.to-main {
    font-family: 'Bungee', cursive;
    text-align: center;
    /* margin-top: 10px; */
    width: 100%;
    padding: 25px;
    background-color: inherit;
    margin-top: 40%;
    cursor: pointer;
    border-top: 1px solid #cccccc;
    border-bottom: 0.5px solid #cccccc;
}

.sidebar label {
    text-align: center;
    font-size: 40px;
    margin-left: 28px;
    font-family: 'Fredoka One', cursive;
}

.to-relation {
    font-family: 'Bungee', cursive;
    /* border-top: 0.5px solid #cccccc; */
    text-align: center;
    width: 100%;
    padding: 25px;
    background-color: inherit;
    cursor: pointer;
    border-bottom: 1px solid #cccccc;
}

/* MainArea.js */
.main-area {
    display: relative;
    /* width: 600px; */
    /* max-width: 70%; */
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    width: 98%;
    padding: 40px;
    background-color: #F1F4FFFF;
    /* border: 1px solid #ffe7ad; */
}

.entire-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.main-area button, .relation-area button {
    -webkit-align-items: center;
            align-items: center;
    margin-bottom: 40px;
    width: 100px;
    padding: 8px;
    border: 1px solid gray;
    border-radius: 3px;
    font-family: 'Space Mono', monospace;
    -webkit-appearance: none;
    appearance: none;
    background: #f2f2f2;
    /* width: 250px; */
    outline: none;
    text-align: center;
}

.input-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    margin-top: 40px;
    margin-bottom: 40px;
}

.input-container input {
    margin-left: 15px;
    margin-right: 15px;
    font-family: 'Space Mono', monospace;
    border: 1px solid #ccc0c0;
    font-size: 17px;
    -webkit-appearance: none;
    appearance: none;
    background: #f2f2f2;
    padding: 10px;
    border-radius: 3px;
    width: 250px;
    outline: none;
    text-align: center;
}

.relation-area {
    position: fixed;
    top:15%;
    left: 38%;
    overflow-y: scroll;
    /* width: 100vw; /* viewport width */
    height: 100vh; /* viewport height */

    scroll-behavior: smooth;
}

.leave {
    font-family: 'Bungee', cursive;
    /* border-top: 0.5px solid #cccccc; */
    text-align: center;
    width: 100%;
    padding: 25px;
    background-color: inherit;
    cursor: pointer;
    border-bottom: 1px solid #cccccc;
}

.hidden {
    position: none;
}

.people-display {
    height:40%;
}

.shadow-pop-bl {
	border-top: 1px solid #cccccc;
	-webkit-animation: shadow-pop-bl 0.1s cubic-bezier(0.470, 0.000, 0.745, 0.715) both;
	        animation: shadow-pop-bl 0.1s cubic-bezier(0.470, 0.000, 0.745, 0.715) both;
}

@-webkit-keyframes shadow-pop-bl {
  0% {
    box-shadow: 0 0 #3e3e3e, 0 0 #3e3e3e, 0 0 #3e3e3e, 0 0 #3e3e3e, 0 0 #3e3e3e, 0 0 #3e3e3e, 0 0 #3e3e3e, 0 0 #3e3e3e;
    -webkit-transform: translateX(0) translateY(0);
            transform: translateX(0) translateY(0);
  }
  100% {
    box-shadow: -1px 1px #3e3e3e, -2px 2px #3e3e3e, -3px 3px #3e3e3e, -4px 4px #3e3e3e, -5px 5px #3e3e3e, -6px 6px #3e3e3e, -7px 7px #3e3e3e, -8px 8px #3e3e3e;
    -webkit-transform: translateX(8px) translateY(-8px);
            transform: translateX(8px) translateY(-8px);
  }
}
@keyframes shadow-pop-bl {
  0% {
    box-shadow: 0 0 #3e3e3e, 0 0 #3e3e3e, 0 0 #3e3e3e, 0 0 #3e3e3e, 0 0 #3e3e3e, 0 0 #3e3e3e, 0 0 #3e3e3e, 0 0 #3e3e3e;
    -webkit-transform: translateX(0) translateY(0);
            transform: translateX(0) translateY(0);
  }
  100% {
    box-shadow: -1px 1px #3e3e3e, -2px 2px #3e3e3e, -3px 3px #3e3e3e, -4px 4px #3e3e3e, -5px 5px #3e3e3e, -6px 6px #3e3e3e, -7px 7px #3e3e3e, -8px 8px #3e3e3e;
    -webkit-transform: translateX(8px) translateY(-8px);
            transform: translateX(8px) translateY(-8px);
  }
}
/* ////////////////////////////////////////////////////////////////////////////////////// */
.scale-up-center {
	-webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

/* ///////////////////////////////////////////////////////////////////////////////// */
.wobble-hor-bottom {
	/* border-top: 1px solid #cccccc; */
	-webkit-animation: wobble-hor-bottom 0.4s both;
	        animation: wobble-hor-bottom 0.4s both;
}

@-webkit-keyframes wobble-hor-bottom {
  0%,
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }
  15% {
    -webkit-transform: translateX(-30px) rotate(-6deg);
            transform: translateX(-30px) rotate(-6deg);
  }
  30% {
    -webkit-transform: translateX(15px) rotate(6deg);
            transform: translateX(15px) rotate(6deg);
  }
  45% {
    -webkit-transform: translateX(-15px) rotate(-3.6deg);
            transform: translateX(-15px) rotate(-3.6deg);
  }
  60% {
    -webkit-transform: translateX(9px) rotate(2.4deg);
            transform: translateX(9px) rotate(2.4deg);
  }
  75% {
    -webkit-transform: translateX(-6px) rotate(-1.2deg);
            transform: translateX(-6px) rotate(-1.2deg);
  }
}
@keyframes wobble-hor-bottom {
  0%,
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }
  15% {
    -webkit-transform: translateX(-30px) rotate(-6deg);
            transform: translateX(-30px) rotate(-6deg);
  }
  30% {
    -webkit-transform: translateX(15px) rotate(6deg);
            transform: translateX(15px) rotate(6deg);
  }
  45% {
    -webkit-transform: translateX(-15px) rotate(-3.6deg);
            transform: translateX(-15px) rotate(-3.6deg);
  }
  60% {
    -webkit-transform: translateX(9px) rotate(2.4deg);
            transform: translateX(9px) rotate(2.4deg);
  }
  75% {
    -webkit-transform: translateX(-6px) rotate(-1.2deg);
            transform: translateX(-6px) rotate(-1.2deg);
  }
}

/* //////////////////////////////////////////////////////////////////////////////////////// */
.fade-in {
	-webkit-animation: fade-in 0.3s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: fade-in 0.3s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

